body {
  background-color: #282c34;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.title{
  text-align: center;
  padding:1px;
  height: 10%
}
.about{
  height: 80%;
  min-height: 40vh;
  align-items: center;
  text-align: center;
}

.Box-Foot{
  height: 10%;
  padding: 10px;
  text-align: center;
}
.row:after {
  content: "";
  display: table;
  clear: both;
}
.column{
  padding:0px;
  float:left;
  width: 33%;
}
.Header-Box{
  background-color: #c1482a;
  margin: 15% 20%;
  align-items: center;
  text-align: center;
  min-height: 40vh;
  min-width: 40vh;
  border-radius: 20px;
  
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

#tsparticles {
  position: fixed;
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  left: 0;
  top: 0;
  z-index: -1;
}
